.contenedor_perfil {
  max-width: 100rem;
  margin: 0 auto;
  width: 90%;

  h1 {
    color: $negro;
    font-weight: $bold;
    font-size: 2rem;
  }
}

#margin0 {
  margin: 0;
}



.galeria {
  height: calc(300px + 3em);
  width: 340px;
  margin: 4rem auto 0rem auto;
  border: 1.5px solid #555;
  position: relative;
  align-items: center;
  justify-content: center;
  // -webkit-box-shadow: 6px 0px 10px 2px rgba(255,87,87,1);
  // -moz-box-shadow: 6px 0px 10px 2px rgba(255,87,87,1);
  // box-shadow: 6px 0px 10px 2px rgba(255,87,87,1);
  border-radius: 0.5rem;

  img {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity 3s;
    height: 95%;
    border-radius: 0.5rem;
  }

  input[type="radio"] {
    position: relative;
    bottom: calc(-300px - 1.5em);
    left: 0.5em;
    accent-color: $principal;
  }
}

