.radiobtn {
  display: flex;
  align-items: center;
}

// .galeria input[type=radio] {
//   position: relative;
//   bottom: calc(-300px - 1.5em);
//   left: .5em;
//   accent-color: $principal;
// }

.galeria input[type="radio"]:nth-of-type(1):checked ~ img:nth-of-type(1) {
  opacity: 1;
}

.galeria input[type="radio"]:nth-of-type(2):checked ~ img:nth-of-type(2) {
  opacity: 1;
}

.galeria input[type="radio"]:nth-of-type(3):checked ~ img:nth-of-type(3) {
  opacity: 1;
}

.galeria input[type="radio"]:nth-of-type(4):checked ~ img:nth-of-type(4) {
  opacity: 1;
}

.alinear_icono {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
  margin-top: 1rem;
}

.perfil_nombre_distancia {
  border-bottom: 2px solid $principal;
  margin-top: 1.5rem;

  .edad {
    font-weight: 350;
  }

  p {
    text-align: center;
    margin: 0;
    font-size: 1.5rem;
    font-weight: 300;
  }
}

.perfil_descripcion {
  margin-top: 1.5rem;

  p {
    text-align: justify;
    font-size: 1.5rem;
    margin: 1rem 0 3rem 0;
  }

  border-bottom: 2px solid $principal;
}

.perfil_sobre_mi {
  border-bottom: 2px solid $principal;
  display: flex;
  justify-content: space-evenly;
}

#h1_sobremi {
  margin: 1.5rem 0 0 0;
  padding: 1rem;
}

.iconos_flex {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.perfil_preferencias {
  margin-top: 1.5rem;
  border-bottom: 2px solid $principal;
  p {
    text-align: center;
    margin: 0;
    font-size: 2rem;
    font-weight: 700;
  }
}

.alinear_icono2 {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 7.5rem;
  margin-top: 1rem;

  border-radius: 2rem;
  background-image: linear-gradient(
    90deg,
    rgba(228, 105, 242, 1) 0%,
    rgba(242, 113, 113, 1) 49%,
    rgba(228, 105, 242, 1) 100%
  );
  padding: 1.5rem;
}

.interereses {
  display: grid;
  grid-template-columns: repeat(2, 0.5fr);
  text-align: center;
  gap: 2rem;
}

.interes_individual{
  border-image: linear-gradient( $principal 0%, #3a4ed5 100%) 1; 
  border: solid ;
  border-radius: 2rem;

}